.skill-card-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 80px 15%;
}
.skill{
    display: flex;
    flex-direction:column;
    width: 120px;
    align-items: center;
    margin-bottom: 30px;
}
.skill>img{
    width: 80px;
    margin-bottom: 15px;
    border-radius: 40px;
    filter: drop-shadow(0px 4px 4px rgba(0,0,0,0.25));
}
.skill>p{
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 24px;
    font-weight: bold;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.015em;
    color: #000;
    margin: 0;
}
.skills-vector-frame{
    position: absolute;
    display: flex;
    flex-direction: row-reverse;
    right: 0;
    bottom: 0;
    z-index: -10;
    width: 80%;
}
.skills-vector{
    max-height: 100%;
    width: 100%;
}
/* Small screen sizes */
@media only screen and (max-width:640px){
    .skills-vector-frame{
        width: 90%;
    }
    .skill-card-container{
        margin: 80px 7.5%;
    }
}